export default {
	data() {
		return {
			srList:[],
			officeList:[],
			headerTop:0,
			//控制滑动效果
			typeClass:'goods',
			subState:'',
			theIndex:null,
			oldIndex:null,
			isStop:false
		}
	},
	onPageScroll(e){
		
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onLoad() {
		uni.request({
		    url: 'http://zy.eatclub.wang:3000/list/sr',
		    method: 'GET',  
		    data: {},  
		    success: res => {  
				// console.log(res.data);
				this.srList = res.data.data;
				// console.log(this.srList);
		    },  
		    fail: () => {},  
		    complete: () => {}
		});
		uni.request({
		    url: 'http://zy.eatclub.wang:3000/list/office',
		    method: 'GET',  
		    data: {},  
		    success: res => {  
				// console.log(res.data);
				this.officeList = res.data.data;
				// console.log(this.officeList);
		    },  
		    fail: () => {},  
		    complete: () => {}
		});
		
		//兼容H5下排序栏位置










	},
	methods: {
		switchType(type){
			if(this.typeClass==type){
				return ;
			}
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
			this.typeClass = type;
			this.subState = this.typeClass==''?'':'show'+type;
			setTimeout(()=>{
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass=='goods'?'':this.subState;
			},200)
		},
		// 自习室办公室跳转
		toStudyroom(e) {
			uni.navigateTo({
				url: '../../goods/goods?srid=' + e
			});
		},
		toOffice(e) {
			uni.navigateTo({
				url: '../../goods/goods?officeid=' + e
			});
		},
	}
}
